import Alertify from "alertifyjs";

export const StringBuilder = require("string-builder");

/**
 * metin deerlerinin doluluunu kontrol eder.
 * undefined veya bo ise true dner.
 * @param {string} text kontrol edilecek metin
 */
export function IsNullOrEmpty(text) {
  return !(text && text.length > 0);
}
/**
 * array ierisindeki elemanlarn null kontrol yaplr.
 * @param {Array} params
 */
export function IsNullOrEmptyAll(params) {
  if (params && params.length > 0) {
    for (const item of params) {
      if (IsNullOrEmpty(item)) return true;
    }
    return false;
  }
  return false;
}

/**
 * obj eer geerli bir obje ise true dner.
 * @param obj
 * @returns {boolean}
 * @constructor
 */
export function IsValidObject(obj) {
  return !!(obj && typeof obj === "object");
}

/**
 * girilen deer geersiz ise -1 dner.
 * geerli ise deeri int oalarak dner
 * @param value
 * @returns {number}
 * @constructor
 */
export function GetIntValue(value) {
  if (value && typeof value === "number") {
    return parseInt(value);
  }
  if (value && typeof value === "string") {
    let val = -1;
    try {
      val = parseInt(value);
      return val;
    } catch (e) {
      return -1;
    }
  } else return -1;
}

/**
 * tarih kontrol yapar.
 * eer bugnden kk veya geersiz ise false dner.
 * @param value
 * @returns {boolean}
 * @constructor
 */
export function DateControlOlderToday(value) {
  // eslint-disable-next-line valid-typeof
  if (value && typeof value === "date") {
    let date = getDateIsoDate();
    return value >= date;
  }
  return false;
}

/**
 * eer item1 < item2 ise ve tarihler geerli ise true dner
 * @param item1
 * @param item2
 * @returns {boolean}
 * @constructor
 */
export function IsBiggerDate(item1, item2) {
  // eslint-disable-next-line valid-typeof
  if (item1 && typeof item1 === "date" && item2 && typeof item2 === "date") {
    return item1 <= item2;
  }
  return false;
}

/**
 * bir ay ncesini verir
 * @param date {Date}
 * @returns {Date}
 * @constructor
 */
export function GetOneMonthAgo(date = new Date()){
  
  if(date.getMonth() === 0){
    var year = date.getFullYear()-1;
    date.setFullYear(year);
    date.setMonth(11);
  }else{
    var month = date.getMonth()-1;
    date.setMonth(month);
  }
  return date;
}

/**
 * is value -1 ? true:
 * @param value
 * @returns {boolean}
 * @constructor
 */
export function IsInvalidIndex(value) {
  return value === -1;
}

