//#region status messages

/**
 * status'ta hata mesaj gsterir.
 * @param message
 * @constructor
 */
export function ShowStatusError(message) {
  Alertify.error(message);
}
/**
 * status'ta bilgi mesaj gsterir.
 * @param message
 * @constructor
 */
export function ShowStatusInfo(message) {
  Alertify.notify(message);
}
/**
 * status'ta baarl mesaj gsterir.
 * @param message
 * @constructor
 */
export function ShowStatusSuccess(message) {
  Alertify.success(message);
}
/**
 * status'ta uyar mesaj gsterir.
 * @param message
 * @constructor
 */
export function ShowStatusWarning(message) {
  Alertify.warning(message);
}
